import React, { useState, version } from 'react';
import styled from 'styled-components';
import PButton from '../../component/PButton';
import EstimateBox from './EstimateBox';

const PlannerEstimate = () => {
  const [myInfo, setMyInfo] = useState({
    name: '',
    phone1: '010',
    phone2: '1234',
    phone3: '1234',
    email: 'example@gmail.com',
    date: '2021.06.12'
  });

  const [companyInfo, setCompanyInfo] = useState({
    name: 'A',
    dress: 'A',
    makeup: 'A',
    weddingHall: true,
    mobileInvitation: true
  });

  const handleMyInfoChange = (e) => {
    setMyInfo({ ...myInfo, [e.target.name]: e.target.value });
  };

  const handleCompanyInfoChange = (e) => {
    setCompanyInfo({ ...companyInfo, [e.target.name]: e.target.value });
  };

  return (
    <Container>
      <Title> </Title>
      <EstimateBox>
        <FlexContainer>
          <div>
            <PlannerName> </PlannerName>
            <CompanyName></CompanyName>
            <Description>       .</Description>
          </div>
          <PButton width="107px" height="44px">
            1:1 
          </PButton>
        </FlexContainer>
      </EstimateBox>
      <EstimateBox>
        <BoxTitle> </BoxTitle>
        <Row>
          <Label></Label>
          <Content>
            <input type="text" value={myInfo.name} name="name" onChange={handleMyInfoChange} />
          </Content>
        </Row>
        <Row>
          <Label></Label>
          <Content>
            <input type="number" value={myInfo.phone1} name="phone1" onChange={handleMyInfoChange} />
            -
            <input type="number" value={myInfo.phone2} name="phone2" onChange={handleMyInfoChange} />
            -
            <input type="number" value={myInfo.phone3} name="phone3" onChange={handleMyInfoChange} />
          </Content>
        </Row>
        <Row>
          <Label></Label>
          <Content>{myInfo.email}</Content>
        </Row>
        <Row>
          <Label> </Label>
          <Content>
            <input type="text" value={myInfo.date} name="date" onChange={handleMyInfoChange} />
          </Content>
        </Row>
      </EstimateBox>
      <EstimateBox>
        <BoxTitle> </BoxTitle>
        <Row>
          <Label></Label>
          <Content>
            <input type="text" value={companyInfo.name} name="name" onChange={handleCompanyInfoChange} />
          </Content>
        </Row>
        <Row>
          <Label></Label>
          <Content>
            <input type="text" value={companyInfo.dress} name="dress" onChange={handleCompanyInfoChange} />
          </Content>
        </Row>
        <Row>
          <Label></Label>
          <Content>
            <input type="text" value={companyInfo.makeup} name="makeup" onChange={handleCompanyInfoChange} />
          </Content>
        </Row>
      </EstimateBox>
      <EstimateBox>
        <BoxTitle></BoxTitle>
        <TextArea placeholder="     . "></TextArea>
      </EstimateBox>
      <EstimateBox>
        <BoxTitle></BoxTitle>
      </EstimateBox>
      <EstimateBox>
        <PButton color="pink"> </PButton>
        <SaveButton></SaveButton>
      </EstimateBox>
      <Empty />
    </Container>
  );
};

export default PlannerEstimate;

const Container = styled.div`
  background-color: #e9ecef;
  padding: 0 290px;
`;

const FlexContainer = styled.div`
  display: flex;
  justify-content: space-between;
`;

const Title = styled.div`
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 20px;
  padding-top: 20px;
`;

const PlannerName = styled.div`
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 4px;
`;

const CompanyName = styled.div`
  font-size: 16px;
  margin-bottom: 12px;
`;

const Description = styled.div`
  font-size: 13px;
  color: #868e96;
`;

const BoxTitle = styled.div`
  font-weight: bold;
  font-size: 16px;
  margin-bottom: 20px;
`;

const Row = styled.div`
  display: flex;
  margin-bottom: 15px;
`;

const Label = styled.label`
  flex: 1;
  font-size: 16px;
  color: #868e96;
`;

const Content = styled.div`
  flex: 5;
`;

const TextArea = styled.textarea`
  width: 100%;
  height: 250px;
`;

const SaveButton = styled.div`
  color: #495057;
  font-size: 16px;
  text-align: center;
  cursor: pointer;
  margin-top: 16px;
`;

const Empty = styled.div`
  background-color: #e9ecef;
  height: 40px;
`;
